import React from 'react';
import ArrowIcon from "./icons/ArrowIcon";

const Block5 = ({openPopup}) => {
    return (
        <div className="block5">
            <h2 className="block5__title">       </h2>
            <div className="block5__btn__container">
                <button onClick={()=>openPopup()} className="block5__btn">  <span className="arrow__span"> <ArrowIcon/></span> </button>
            </div>
        </div>
    );
};

export default Block5;