import React from 'react';
import LogoAxioma from "./icons/LogoAxioma";

const Header = ({openPopup}) => {
    return (
        <header className="header">
            <div className="header__container">
                <div className="header__logo__container">
                    <LogoAxioma/>
                </div>
                <div className="header__text__container">
                    <a href="#weDo" className="header__text">  </a>
                    <a href="#questions" className="header__text">  </a>
                    <a href="#contacts" className="header__contact"></a>
                    <button onClick={()=>openPopup()} className="header__btn"> </button>
                </div>
            </div>
        </header>
    );
};

export default Header;