import React from 'react';
import Icon1 from "./icons/icon-1";
import Icon2 from "./icons/Icon-2";
import Icon3 from "./icons/icon-3";
import Card from "./Card";
import Box1 from "./Parallax/Box1";

const Block2 = () => {
    const state = [
        {
            id:1,
            icon:<Icon1/>,
            title:"",
            text:"               (Google, Facebook, Instagram  .)"
        }, {
            id:2,
            icon:<Icon2/>,
            title:"",
            text:"    ,       ,      ,   . \n"
        },{
            id:3,
            icon:<Icon3/>,
            title:" ",
            text:"          . Axioma Partners    .     - !"
        },
    ];
    return (
        <div id="weDo" className="block2">
            <h3 className="block2__title">  ?</h3>
            <div className="card__container">
            {state.map((item,index)=>(
                <React.Fragment key={index}>
                    <Card  item={item}/>
                </React.Fragment>
            ))}
            </div>
            <Box1/>
        </div>
    );
};

export default Block2;